.mask{
  width: 100%;
  height: 100vh; 
  position: relative;

}

.hero_image{
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center; 
  object-fit: cover;
}

.mask::after{
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top:0;
  left: 0;
  background-color: #000;
  opacity: 0.4;
}

.hero {
  height: 100%;
  width: 100%;
}
.hero .hero_content {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}

.hero .hero_content h1{
  font-size: 4rem;
  padding: 0.6rem 0 1.5rem;
}

.hero .hero_content p{
  font-size: 1.4rem;
  font-weight: 200;
  text-transform: uppercase;
}


@media screen and (max-width: 640px ) {
  
  .hero .hero_content h1{
    font-size: 3rem;
   
  }
  
  .hero .hero_content p{
    font-size: 1.4rem;
   
  }
}